'use strict';

require('xterm/dist/xterm.css');
require('../css/gritty.css');

require('xterm/dist/addons/fit');

const currify = require('currify/legacy');

const getEl = require('./get-el');
const getHost = require('./get-host');
const getEnv = require('./get-env');
const timeout = require('./timeout');
const wrap = require('wraptile/legacy');
const userinfo = require('./user.js');


const onConnect = wrap(_onConnect);
const onAuth = currify(_onAuth);
const onTFile = currify(_onTFile);
const onError1 = currify(_onError1);
const onDisconnect = wrap(_onDisconnect);
const onData = currify(_onData);
const onTermResize = currify(_onTermResize);
const onTermData = currify(_onTermData);
const onWindowResize = wrap(_onWindowResize);

const io = require('socket.io-client/dist/socket.io.min');

window.Promise = window.Promise || require('promise-polyfill');
window.fetch = window.fetch || require('whatwg-fetch');

const Terminal = require('xterm/dist/xterm');

module.exports = gritty;
module.exports._onConnect = _onConnect;
module.exports._onAuth = _onAuth;
module.exports._onError1 = _onError1;
module.exports._onDisconnect = _onDisconnect;
module.exports._onData = _onData;
module.exports._onTermResize = _onTermResize;
module.exports._onTermData = _onTermData;
module.exports._onWindowResize = _onWindowResize;
module.exports._init_cmds = "pwd\n";
module.exports._callback = null;
module.exports._data = "";
module.exports._socket = "";
module.exports._href = null;


function gritty(element, options = {}) {
    const el = getEl(element);

    const socketPath = options.socketPath || '';
    const prefix = options.prefix || '/gritty';
    const env = getEnv(options.env || {});

    module.exports._href = options.href;

    const socket = connect(prefix, socketPath);
    module.exports._callback = options.callback;
    module.exports._socket = socket;
    return createTerminal(el, {
        env,
        socket,
    });
}

function createTerminal(terminalContainer, { env, socket }) {
    const terminal = new Terminal({
        scrollback: 1000,
        tabStopWidth: 4,
        theme: 'gritty',
    });

    terminal.open(terminalContainer);
    terminal.fit();

    socket.on('auth', onAuth(socket));
    socket.on('tfile', onTFile(socket));
    socket.on('error1', onError1(socket));
    terminal.on('resize', onTermResize(socket));
    terminal.on('data', onTermData(socket));

    window.addEventListener('resize', onWindowResize(terminal))

    const { cols, rows } = terminal.proposeGeometry()

    // auth check delay
    socket.on('connect', timeout(onConnect(socket, { env, cols, rows })));
    socket.on('disconnect', onDisconnect(terminal));
    socket.on('data', onData(terminal));

    return {
        socket,
        terminal
    };
}

function _onConnect(socket, { env, cols, rows }) {
    socket.emit('terminal', { env, cols, rows });
    socket.emit('resize', { cols, rows });
    console.log("_onConnect");
    //socket.emit('data', module.exports._init_cmds);
    socket.emit('gitcmd','initcmd');
}

function _onDisconnect(terminal) {
    terminal.writeln('terminal disconnected...');
}

function _onData(terminal, data) {
    terminal.write(data);
    module.exports._data += data;
    var pattern = /bringup@bringup-VirtualBox:.*?\[00m\$/;
    var str = module.exports._data;
    while (true) {
        var re = new RegExp(/bringup@bringup-VirtualBox:/);
        var tmp = re.exec(str);
        if (tmp) {
            var subindex = tmp.index + tmp[0].length;
            var text = str.substring(0, tmp.index);
            console.log(text);
            var tmpcmd = text.split('\n')[0]
            str = str.substring(subindex);
            var tmp2 = str.match(/\[00m\$/);
            subindex = tmp2.index + tmp2[0].length;
            str = str.substring(subindex);
            module.exports._data = str;

            if(/^\sgit status/.test(tmpcmd)){
                module.exports._callback("status",text);
            }
        } else {
            break;
        }
    }
}

function _onAuth(socket, data) {
    if (data == 'getapikey') {
        userinfo((api_key) => {
            socket.emit('auth', window.location.search + "&api_key=" + api_key);
        });
    } else if(data=='errorurl'){
        alert("URL ERROR!");
    }else {
        module.exports._init_cmds = data;
        console.log(data);
    }
}

function _onTFile(socket, data) {
    module.exports._callback("file",data);
}

function _onError1(socket, data) {
    module.exports._callback("error1",data);
}

function _onTermResize(socket, { cols, rows }) {
    socket.emit('resize', { cols, rows });
}

function _onTermData(socket, data) {
    socket.emit('data', data);
}

function _onWindowResize(terminal) {
    terminal.fit();
}

function connect(prefix, socketPath) {
    var href = getHost();
    if(module.exports._href){
        href = module.exports._href;
    }
    const FIVE_SECONDS = 5000;

    const path = socketPath + '/socket.io';
    const socket = io.connect(href + prefix, {
        'max reconnection attempts': Math.pow(2, 32),
        'reconnection limit': FIVE_SECONDS,
        path
    });

    return socket;
}

module.exports.tFile = (fname,ftxt,fop)=>{
    var fobj = {
        name:fname,
        txt:ftxt,
        op:fop
    }
    module.exports._socket.emit('tfile',JSON.stringify(fobj));
}

module.exports.commitall = ()=>{
    module.exports._socket.emit('gitcmd','commitcmd');
}


