'use strict';
import $ from 'jquery';
//?key=925b858c9eb42103d83c4e1e95df2844fa05dcb8
module.exports = (func) => {
    $.ajax({
        url: "http://api.redmine.meizu.com/users/current.json",
        dataType: 'jsonp',
        data: '',
        jsonp: 'callback',
        success: function (result) {
            //console.log(result.user.api_key);
            //module.exports.api_key = result.user.api_key;
            func(result.user.api_key);
        },
        timeout: 3000
    });
}



